.contact {
  background-color: var(--primary-dark);
  padding: 80px 0;
  text-align: center;
  position: relative;
  overflow: hidden;
}

.contact::before {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: radial-gradient(circle, var(--secondary-accent) 0%, transparent 70%);
  opacity: 0.1;
  animation: rotate 20s linear infinite;
}

@keyframes rotate {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.contact h2 {
  color: var(--primary-accent);
  font-size: 3rem;
  margin-bottom: 30px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  text-transform: uppercase;
  letter-spacing: 3px;
  position: relative;
}

.contact p {
  color: var(--text-light);
  font-size: 1.3rem;
  margin-bottom: 40px;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}

.contact .cta-button {
  display: inline-block;
  padding: 18px 36px;
  background-color: var(--primary-accent);
  color: var(--text-light);
  text-decoration: none;
  font-size: 1.3rem;
  font-weight: bold;
  border-radius: 8px;
  transition: all 0.4s ease;
  text-transform: uppercase;
  letter-spacing: 2px;
  position: relative;
  overflow: hidden;
}

.contact .cta-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: all 0.4s;
}

.contact .cta-button:hover::before {
  left: 100%;
}

.contact .cta-button:hover {
  background-color: var(--secondary-accent);
  transform: translateY(-5px) scale(1.05);
  box-shadow: 0 15px 30px rgba(255, 61, 0, 0.6);
}

@media (max-width: 768px) {
  .contact h2 {
    font-size: 2.5rem;
  }

  .contact p {
    font-size: 1.1rem;
    padding: 0 20px;
  }

  .contact .cta-button {
    font-size: 1.1rem;
    padding: 15px 30px;
  }
}